body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.viewContainer {
    margin-left: 250px;
    padding: 15px;
}

.viewContainer p {
    text-align: left;
}


    @media only screen and (max-width: 768px) {
        .viewContainer {
            margin-left: 110px; 
        }
    }
.nav {
    height: 100%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #AED6F1;
    overflow-x: hidden;
    padding-top: 20px;
}

.nav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    text-align: center;
  }

.nav a:hover {
    color: #f1f1f1;
}

.avatar {
    max-width: 200px;
    max-height: 200px;
    border-radius: 100%;
    margin: 25px;
}

@media only screen and (max-width: 768px) {
    .sideNav {
        width: 100px;
    }
    .sideNav a {
        font-size: 15px; 
    }
    .avatar {
        max-width: 75px;
        max-height: 75px;
        border-radius: 100;
        margin-left: 15px;
    }
} 

.contact {
    margin-left: 250px;
    padding: 15px;
    max-width: 35%;
    text-align: left;
}

.contact a {
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-size: 18px;
  color: black;
  display: block;
}

.contact a:hover {
  color: #818181;
}

.email-address {
  padding: 6px 8px 6px 16px;
  font-size: 18px;
}

input[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  input[type=submit] {
    width: 75px;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  input[type=submit]:hover {
    background-color: #45a049;
  }

textarea {
    width: 100%;
    height: 150px;
    margin: 8px 0;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
  }

  @media only screen and (max-width: 768px) {
    .contact {
      margin-left: 110px;
    }
  }
.viewContainer {
    margin-left: 250px;
    padding: 15px;
    text-align: center;
}

.projectBox {
    width: 200px;
    height: 200px;
    float: left;
    position: relative;
    /* text-align: center; */
    margin: 20px;
}

.projectLabel {
    color: #FDFEFE;
    text-align: center;
    font-size: medium;
    background-color: #aed6f1;
    width: 192px;
    height: 30px;
    padding: 4px;
    position: absolute;
    top: 80%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    
}

.projectPhoto {
    width: 200px;
    height: 200px;
    float: left;
    position: relative;
    text-align: center;   
}

.move {
    padding-left: 45px;
    box-sizing: content-box;
}

@media only screen and (max-width: 768px) {
    .move {
        padding-left: 130px;
        box-sizing: content-box;
    }
    .viewContainer {
        margin-left: 110px; 
    }
} 
